import { InputLabel, MenuItem, Select, SelectChangeEvent } from "@rp/react/web/mui/core"
import { useFeedbackContext } from "./FeedbackContext"
import { FeedbackForm, FormControlStyled } from "./FeedbackMode.styles"
import { SubTypes, subTypeOptions } from "./feedback.types"

export default function PersonaSubTypeForm() {
  const { subType, setSubType } = useFeedbackContext()

  const handleChange = (event: SelectChangeEvent) => {
    setSubType(event.target.value as SubTypes)
  }

  const options = subTypeOptions.map(option => (
    <MenuItem key={option.value} value={option.value}>
      {option.label}
    </MenuItem>
  ))

  return (
    <FeedbackForm>
      <FormControlStyled>
        <InputLabel id="persona-sub-type-label">Persona sub-type</InputLabel>
        <Select
          disabled
          value={subType}
          label="persona-sub-type"
          onChange={handleChange}
          inputProps={{
            "data-testid": "sub-type-options-id",
          }}
        >
          {options}
        </Select>
      </FormControlStyled>
    </FeedbackForm>
  )
}
